import React, {useState} from 'react';
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";


const UserHeader = () => {
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [userName, setUserName] = useState('');


    //    
    const handleLogin = () => {
        setIsLoggedIn(true);
        setUserName('John'); //  'John'   .
    };

    //    
    const handleLogout = () => {
        setIsLoggedIn(false);
        setUserName('');
    };

    return (
        <header>
            {isLoggedIn ? (
        <Card style={{width: '300px', height: '100px'}}>
            <Card.Header style={{fontSize: '16px', padding: '2px'}}></Card.Header>
            <Card.Body style={{fontSize: '14px', padding: '2px'}}>
                <Card.Title style={{fontSize: '18px', marginBottom: '8px'}}>, {userName} </Card.Title>
                <Button variant="primary" size="sm" onClick={handleLogout}>  </Button>
                <Button variant="primary" size="sm" style={{marginLeft: '30px'}}>    </Button>
            </Card.Body>
        </Card>
                ):(
                <button onClick={handleLogin}></button>
            )}
        </header>
    );
};

export default UserHeader;
